import React from "react";
import Leaf from "./Leaf.mp4";

export function LeafVideo() {
  return (
    <div>
      <video
        autostart
        autoPlay
        src={Leaf}
        className="vidContainer"
        // controls
        onMouseOver={(event) => event.target.play()}
        onMouseOut={(event) => event.target.pause()}
        loop
        id="leafVideo"
        type="video/mp4"
      ></video>
    </div>
  );
}
export default LeafVideo;
